.wrapper{
    width: 420px;
    background: transparent;
    color: #fff;
    border: 2px solid rgba(255,255,255,.2);
    box-shadow: 0 0 20px rgba(0,0,0,.2);
    backdrop-filter: blur(10px);
    border-radius: 10px;
    padding: 30px 40px;
    
}
.wrapper h1{
    text-align: center;
    font-size: 36px;
    
}
.wrapper .input-box{
    position: relative;
    width: 100%;
    height: 50px;
    margin: 30px 0;
}
.input-box input{
    width: 100%;
    height: 100%;
    background: transparent;
    border: none;
    outline: none;
    border: 2px solid rgba(255,255 ,255,.2);
    border-radius: 20px;
    color: #fff;
    font-size: 16px;
    padding: 20px 45px 20px 20px;
}
.input-box input::placeholder{
    color: #fff;
}
.input-box .icon{
    position: absolute;
    right: 20px;
    color: #fff;
    top: 50%;
    transform: translateY(-50%);
    font-size: 16px;
}
.wrapper .remember-forgot{
    display: flex;
    justify-content: space-between;
    font-size: 14px;
    margin: -15px 0 15px ;
}
.remember-forgot label input{
    accent-color: #fff;
    margin-right: 4px;
}
.remember-forgot a{
    color: #fff;
    text-decoration: none;
}
.remember-forgot a:hover{
    text-decoration: underline;
}
.wrapper button{
    width: 100%;
    height: 50px;
    background: #fff;
    border: none;
    outline: none;
    border-radius: 20px;
    box-shadow: 0 0 10px rgba(0,0,0,.1);
    cursor: pointer;
    font-size: 18px;
    color: #333;
    font-weight: 700;
    transition: all .3s ease;
}